	module.exports = {"data":{"post":{"html":"<p>Mama's under the weather, so Christopher and I hiked up to Hamburg for the afternoon to visit the Christmas markets downtown. I decided that we would take the train rather than drive, and thus give Christopher his first ride in a real live Deutsche Bahn train (as opposed to a mere S- or U-Bahn). I had forgotten that it was not only cold but windy (so it was miserable outside at the market), and that there was a soccer match so that the main station was full of HSV fans (so we missed the train I wanted to take home and had to wait an hour for the next one), and that the return train would be packed to the brim with returning weekend travelers (so we had to stand... ever try to get a 3-year-old to stand still for 25 minutes?). In return, the DB never checked our tickets, which I was planning to buy on board, so the trip was relatively inexpensive.</p>\n<p>When we got home, Christopher told Mama all about trains for a good half hour. So I guess he enjoyed the trip.</p>","fields":{"title":"Cold Legs","date":"08 Dec 2002","url":"https://papascott.de/2002-12-08-cold-legs/"}}},"pathContext":{"slug":"/2002-12-08-cold-legs/","prev":{"fields":{"title":"Party Like It's 1968","date":"07 Dec 2002","slug":"/2002-12-07-party-like-its-1968/"}},"next":{"fields":{"title":"Advent Calendar at leo.org","date":"09 Dec 2002","slug":"/2002-12-09-advent-calendar-at-leoorg/"}}}}

